
@import 'app/sass/reset';
@import 'app/sass/icon-font.scss';

body {
    background:#333;
    width: 100%;
    margin: 0;
    padding: 0;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}

div {
    display: flex;
    flex-direction: column;

    h1 {
        color: #dfd553;
        transform: rotate(180deg);
    }

    p {
        color: #fff;
        padding: 2rem;
    }
}

.second {
    flex-direction:row;
    
    .img {
        width:100%;
        height: 100px;
        background-image: url(/images/rocks-thum.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    p {
        color: rgb(161, 157, 157);
    }

    h2 {
        color: orange;
        text-decoration: underline;
        &::before {
            content: '\e900';
            font-family: 'my-practice-icon';
            color: white;
        }
    }
}